import type { Business, Conversation, AuthUser } from "./types"

const BUSINESSES_KEY = "leenk_businesses"
const CONVERSATIONS_KEY = "leenk_conversations"
const AUTH_KEY = "leenk_auth"

export const storage = {
  // Businesses
  getAllBusinesses: (): Business[] => {
    const data = typeof window !== "undefined" ? localStorage.getItem(BUSINESSES_KEY) : null
    return data ? JSON.parse(data) : []
  },

  getBusinessByPhone: (phone: string): Business | null => {
    const businesses = storage.getAllBusinesses()
    return businesses.find((b) => b.phone === phone) || null
  },

  getBusinessById: (id: string): Business | null => {
    const businesses = storage.getAllBusinesses()
    return businesses.find((b) => b.id === id) || null
  },

  createBusiness: (business: Business): void => {
    const businesses = storage.getAllBusinesses()
    businesses.push(business)
    if (typeof window !== "undefined") {
      localStorage.setItem(BUSINESSES_KEY, JSON.stringify(businesses))
    }
  },

  updateBusiness: (id: string, updates: Partial<Business>): void => {
    const businesses = storage.getAllBusinesses()
    const index = businesses.findIndex((b) => b.id === id)
    if (index !== -1) {
      businesses[index] = { ...businesses[index], ...updates }
      if (typeof window !== "undefined") {
        localStorage.setItem(BUSINESSES_KEY, JSON.stringify(businesses))
      }
    }
  },

  // Conversations
  getConversationsByBusinessId: (businessId: string): Conversation[] => {
    const data = typeof window !== "undefined" ? localStorage.getItem(CONVERSATIONS_KEY) : null
    const conversations = data ? JSON.parse(data) : []
    return conversations.filter((c: Conversation) => c.businessId === businessId)
  },

  getConversationById: (id: string): Conversation | null => {
    const data = typeof window !== "undefined" ? localStorage.getItem(CONVERSATIONS_KEY) : null
    const conversations = data ? JSON.parse(data) : []
    return conversations.find((c: Conversation) => c.id === id) || null
  },

  createConversation: (conversation: Conversation): void => {
    const data = typeof window !== "undefined" ? localStorage.getItem(CONVERSATIONS_KEY) : null
    const conversations = data ? JSON.parse(data) : []
    conversations.push(conversation)
    if (typeof window !== "undefined") {
      localStorage.setItem(CONVERSATIONS_KEY, JSON.stringify(conversations))
    }
  },

  updateConversation: (id: string, updates: Partial<Conversation>): void => {
    const data = typeof window !== "undefined" ? localStorage.getItem(CONVERSATIONS_KEY) : null
    const conversations = data ? JSON.parse(data) : []
    const index = conversations.findIndex((c: Conversation) => c.id === id)
    if (index !== -1) {
      conversations[index] = { ...conversations[index], ...updates }
      if (typeof window !== "undefined") {
        localStorage.setItem(CONVERSATIONS_KEY, JSON.stringify(conversations))
      }
    }
  },

  // Auth
  getAuth: (): AuthUser | null => {
    const data = typeof window !== "undefined" ? localStorage.getItem(AUTH_KEY) : null
    return data ? JSON.parse(data) : null
  },

  setAuth: (user: AuthUser): void => {
    if (typeof window !== "undefined") {
      localStorage.setItem(AUTH_KEY, JSON.stringify(user))
    }
  },

  clearAuth: (): void => {
    if (typeof window !== "undefined") {
      localStorage.removeItem(AUTH_KEY)
    }
  },
}
