"use client"

import type React from "react"

import { useState, useRef } from "react"
import { Button } from "@/components/ui/button"
import { Paperclip, Send } from "lucide-react"
import { motion } from "framer-motion"

interface MessageInputProps {
  onSendMessage: (text: string) => void
  onSendImage: (imageUrl: string) => void
  disabled?: boolean
}

export function MessageInput({ onSendMessage, onSendImage, disabled }: MessageInputProps) {
  const [message, setMessage] = useState("")
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleSend = () => {
    if (message.trim()) {
      onSendMessage(message)
      setMessage("")
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault()
      handleSend()
    }
  }

  const handleImageSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onload = (event) => {
        const imageUrl = event.target?.result as string
        onSendImage(imageUrl)
      }
      reader.readAsDataURL(file)
    }
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="border-t border-border bg-card p-4 flex gap-2"
    >
      <button
        onClick={() => fileInputRef.current?.click()}
        disabled={disabled}
        className="p-2 hover:bg-secondary rounded-lg transition-colors disabled:opacity-50"
        aria-label="Upload image"
      >
        <Paperclip className="w-5 h-5" />
      </button>
      <input ref={fileInputRef} type="file" accept="image/*" onChange={handleImageSelect} className="hidden" />
      <textarea
        value={message}
        onChange={(e) => setMessage(e.target.value)}
        onKeyPress={handleKeyPress}
        placeholder="Type a message..."
        disabled={disabled}
        className="flex-1 bg-input border border-border rounded-lg px-4 py-2 resize-none focus:outline-none focus:ring-2 focus:ring-primary disabled:opacity-50"
        rows={2}
      />
      <Button onClick={handleSend} disabled={!message.trim() || disabled} size="sm" className="self-end">
        <Send className="w-4 h-4" />
      </Button>
    </motion.div>
  )
}
